<template>
  <div class="Pos">
    <el-row>

      <el-col :span="7">
       <el-tabs>
         <el-tab-pane label="点菜">

           <el-table :data="tableData" border >
              <el-table-column prop="foodsName" label="商品">
              </el-table-column>
              <el-table-column prop="count" label="数量" width="50">
              </el-table-column>
             <el-table-column prop="price" label="金额"  width="70">
             </el-table-column>
              <el-table-column label="操作" width="100" fixed="right">
                  <template scope="scope">
                      <el-button type="text" size="small" v-on:click="delGoods(scope.row)">
                        删除
                      </el-button>
                      <el-button type="text" size="small" v-on:click="addOrderList(scope.row)">
                        增加
                      </el-button>
                  </template>
              </el-table-column>
           </el-table>
           <div>
             <span v-html="'总数量：'+totalCount" ></span>
             <span v-html="'总价格：'+totalMoney"></span>
           </div>
           <div>
             <el-button type="primary" size="small"
                        @click="delAllGoods()">
               清空购物车
             </el-button>
           </div>

         </el-tab-pane>
         <el-tab-pane label="外卖">

         </el-tab-pane>
         <el-tab-pane label="自取">

         </el-tab-pane>
       </el-tabs>
      </el-col>

      <el-col :span="17">
          <div class="daily-goods">
              <div class="title">日常用品</div>
            <div class="daily-goods-list">
              <ul>
                <li v-for="goods in oftenGoods" v-on:click="addOrderList(goods)">
                    <span v-html="goods.foodsName"></span>
                    <span v-html="'￥' + goods.price"></span>
                </li>
              </ul>
            </div>
          </div>

          <div class="goods-type">
            <el-tabs>

              <el-tab-pane label="套餐一">
                <ul class="cookList">
                  <li v-for="goods in foods_type" v-on:click="addOrderList(goods)">
                    <span class="foodImg">
                      <img :src="goods.goodsImg" width="100%"/>
                    </span>
                    <span class="foodName" v-html="goods.foodsName"></span>
                    <span class="foodPrice" v-html="'￥' + goods.price"></span>
                  </li>
                </ul>
              </el-tab-pane>

              <el-tab-pane label="套餐二">
                <ul class="cookList">
                  <li v-for="goods in foods_type1" v-on:click="addOrderList(goods)">
                      <span class="foodImg">
                        <img :src="goods.goodsImg" width="100%"/>
                      </span>
                    <span class="foodName" v-html="goods.foodsName"></span>
                    <span class="foodPrice" v-html="'￥' + goods.price"></span>
                  </li>
                </ul>
              </el-tab-pane>

              <el-tab-pane label="套餐三">
                <ul class="cookList">
                  <li v-for="goods in foods_type2" v-on:click="addOrderList(goods)">
                        <span class="foodImg">
                          <img :src="goods.goodsImg" width="100%"/>
                        </span>
                    <span class="foodName" v-html="goods.foodsName"></span>
                    <span class="foodPrice" v-html="'￥' + goods.price"></span>
                  </li>
                </ul>
              </el-tab-pane>

              <el-tab-pane label="套餐四">
                <ul class="cookList">
                  <li v-for="goods in foods_type3" v-on:click="addOrderList(goods)">
                        <span class="foodImg">
                          <img :src="goods.goodsImg" width="100%"/>
                        </span>
                    <span class="foodName" v-html="goods.foodsName"></span>
                    <span class="foodPrice" v-html="'￥' + goods.price"></span>
                  </li>
                </ul>
              </el-tab-pane>


            </el-tabs>
          </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default{
    name: 'Pos',
    data(){
      return{
        tableData:[],

        oftenGoods:[{
          foodsName:'香辣鸡腿堡',
          goodsId:1,
          price:15
        },{
          foodsName:'迷你全家桶',
          goodsId:2,
          price:115
        },{
          foodsName:'豪华全家桶',
          goodsId:3,
          price:125
        }],

        foods_type:[{
          goodsId:11,
          goodsImg:"http://pic2.16pic.com/00/05/57/16pic_557962_b.jpg",
          foodsName:'汉堡套餐1',
          price:18
        }, {
          goodsId:12,
          goodsImg:"https://gd3.alicdn.com/imgextra/i3/2485892750/TB2x9RFbNXlpuFjSsphXXbJOXXa_!!2485892750.jpg",
          foodsName:'炸鸡套餐1',
          price:15
        }, {
          goodsId:13,
          goodsImg:"http://file06.16sucai.com/2016/0301/3e5a68f7afd2f172577dba8e847b41d0.jpg",
          foodsName:'烤肉套餐1',
          price:15
        }],

        foods_type1:[
          {
            goodsId:14,
            goodsImg:"http://file06.16sucai.com/2016/0301/59cf0b8f830895bdb2b1cde6d2bbd78e.jpg",
            foodsName:'汉堡套餐2',
            price:80
          }, {
            goodsId:15,
            goodsImg:"http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg",
            foodsName:'豪华午餐套餐2',
            price:10
          }
        ],
        foods_type2:[
          {
            goodsId:16,
            goodsImg:"http://file06.16sucai.com/2016/0301/59cf0b8f830895bdb2b1cde6d2bbd78e.jpg",
            foodsName:'精品套餐3',
            price:20
          }
        ],
        foods_type3:[
          {
            goodsId:17,
            goodsImg:"https://gd3.alicdn.com/imgextra/i3/2485892750/TB2x9RFbNXlpuFjSsphXXbJOXXa_!!2485892750.jpg",
            foodsName:'炸鸡套餐4',
            price:10
          }, {
            goodsId:18,
            goodsImg:"http://file06.16sucai.com/2016/0301/3e5a68f7afd2f172577dba8e847b41d0.jpg",
            foodsName:'烤肉套餐4',
            price:18
          }, {
            goodsId:19,
            goodsImg:"http://pic2.16pic.com/00/05/57/16pic_557962_b.jpg",
            foodsName:'汉堡套餐4',
            price:15
          }
        ],
        totalCount: 0,
        totalMoney: 0
      }
    },
    methods:{
      // 增加商品到订单列表
      addOrderList(goods){
        // 开关，用来判断某商品是否存在于订单之中
        let _is = false;

        // 判断订单之中，是否已经存在某商品
        for(let i=0; i<this.tableData.length; i++){
          if(this.tableData[i].goodsId === goods.goodsId){
            _is = true;
            break;
          }
        }
        //如果存在就将数量+1
        if(_is === true){
          let _temp = this.tableData.filter(_g => _g.goodsId === goods.goodsId);

          _temp[0].count++
        }else{
          // 某商品不在订单之中
          let newGoods = {
            goodsId: goods.goodsId,
            foodsName: goods.foodsName,
            price: goods.price,
            count: 1
          };
          this.tableData.push(newGoods)
        }
        this.getAllMoney()
      },

      // 删除单个商品
      delGoods(goods){
        this.tableData = this.tableData.filter(_g => _g.goodsId !== goods.goodsId)
        this.getAllMoney()
      },

      // 总金额和总数量
      getAllMoney(){
        this.totalCount = 0;
        this.totalMoney = 0;

        this.tableData.forEach(_d => {
          this.totalCount += _d.count;
          this.totalMoney = this.totalMoney +(_d.count * _d.price)
        })
      },

      //清空购物车
      delAllGoods(){
        this.totalCount = 0;
        this.totalMoney = 0;
        this.tableData = [];
      }
    }
  }
</script>

<style>
  .title{
    height: 20px;
    border-bottom:1px solid #D3DCE6;
    background-color: #F9FAFC;text-alien:left;
    padding:10px;
  }
  .daily-goods-list ul li{
    list-style: none;
    float:left;
    border:1px solid #E5E9F2;
    padding:10px;
    margin:5px;
    background-color:#fff;
  }
  .o-price{
    color:#58B7FF;
  }
  .goods-type{clear: both;}
  .cookList li{
    list-style: none;
    width:23%;
    border:1px solid #E5E9F2;
    height: auto;
    overflow: hidden;
    background-color:#fff;
    padding: 2px;
    float:left;
    margin: 2px;

  }
  .cookList li span{
    display: block;
    float:left;
  }
  .cookList li span.foodImg{
    width:100px;height:100px;overflow:hidden;
    display: block;
    float:left;
  }
  .foodImg{
    width: 40%;
  }
  .foodName{
    font-size: 18px;
    padding-left: 10px;
    color:brown;

  }
  .foodPrice{
    font-size: 16px;
    padding-left: 10px;
  }
</style>



